//Definindo as vari√°veis que ser√£o utilizadas
let _seed: number;
let validator = true;
let payload = {};
let numbers = [
  "0.3156106622882362",
  "0.5513213466665547",
  "0.3301843000590452",
  "0.04769233684601204",
  "0.4450511749319048",
  "0.8014283726497524",
  "0.6599671903586568",
  "0.39207733233979525",
  "0.9243979984480575",
];
//Atribuindo √† vari√°vel _seed, j√° definida, um valor que foi passado como atributo da fun√ß√£o
export function seed(seed: number) {
  _seed = seed;
}

export function random() {
  //Determinando o seno da seed passada pelo usu√°rio e a multiplicando por 10000
  const x = Math.sin(_seed++) * 10000;
  //Subtraindo do valor de X a sua parte inteira, deixando apenas as casas depois da v√≠rgula, e retornando ao usu√°rio
  return x - Math.floor(x);
}

export function checkRandomNumber(array: Array<number>) {
  array.forEach((element: number) => {
    //Iterando cada valor do array enviado como atributo da fun√ß√£o
    numbers.includes(element.toString()) //Verificando se determinado elemento do array est√° em numbers
      ? console.log("equals") //Se o elemento estiver em Numbers ele imprime "equals" no console
      : (validator = false); //Se o elemento n√£o estiver em Numbers ele define a vari√°vel validator como falsa
  });

  validator //Verificando o valor de validator
    ? (payload = JSON.parse('{ "status": "success"}')) //Se for verdadeira a vari√°vel payload recebe um objeto com a propriedade "status" definida como "success"
    : (payload = JSON.parse('{ "status": "error"}')); //Se for falsa a vari√°vel payload recebe um objeto com a propriedade "status" definida como "error"

  return payload; //Retorna o valor de payload
}
